import { AgGridVue } from 'ag-grid-vue'
import localeText_ko from '@/locales/localeText_ko'

export default {
  name: 'AccountBook',
  components: {
    AgGridVue,
  },
  data() {
    return {
      gridApi: null,
      columnDefs: [
        { headerName: '날짜', field: 'date', sortable: true, filter: true, editable: true },
        { headerName: '물건', field: 'item', sortable: true, filter: true, rowGroup: true, hide: true },
        { headerName: '금액 (₩)', field: 'amount', sortable: true, filter: true, editable: true, type: 'numericColumn', aggFunc: 'sum' },
        { headerName: '메모', field: 'note', sortable: true, filter: true, editable: true },
      ],
      rowData: this.loadData(), // LocalStorage에서 데이터를 불러오기
      totalAmount: 0, // 합계를 저장하는 변수
      displayData: [], // 그리드에 표시할 데이터 (실제 데이터 + 소계)
      autoGroupColumnDef: {
        headerName: '물건 그룹', // 그룹 열의 헤더 이름
        field: 'item',
        cellRenderer: 'agGroupCellRenderer', // 그룹 셀 렌더러
        // cellRendererParams: {
        //   footerValueGetter: (params) => {
        //     // const field = params.column.getColId()
        //     // const value = params.value || ''
        //     // return `${value} 총합계: ${params.data[field]}`
        //     if (!params.node.aggData) return null;
        //     return `총 합계: ${params.node.aggData['amount']}`
        //   },
        // },
        // cellClass: function(params) {
        //   if(params.data.someConditionForCellsToBeMerged) {
        //     return params.data.someConditionForCellToKeep ? "top-row-span": "bottom-row-span";
        //   }   
        // }
      },
      defaultColDef: {
        editable: true,
        sortable: true,
        resizable: true,
      },
      // groupIncludeFooter: true, // 그룹 하단에 소계를 표시
      localeText: localeText_ko,
    }
  },
  methods: {
    // 그리드가 준비되었을 때 호출되는 메서드
    onGridReady(params) {
      this.gridApi = params.api
      this.calculateTotals()
    },

    // 셀 편집이 끝났을 때 호출되는 메서드
    onCellEditEnded(event) {
      const editedField = event.colDef.field
      const newValue = event.value
      const rowIndex = event.rowIndex
      const rowNode = event.node

      console.log(`Cell editing ended on field: ${editedField}, row: ${rowIndex}, new value: ${newValue}`)
      
      // amount 값이 0인 경우 note 필드를 'free'로 설정
      if (editedField === 'amount' && newValue === 0) {
        rowNode.setDataValue('note', 'free')
      }
      this.calculateTotals()
      // 데이터가 편집된 후 LocalStorage에 저장
      this.saveData()
    },

    // 빈 행을 추가하는 메서드
    addEmptyRow() {
      const emptyRow = {
        date: '',
        item: '',
        amount: null,
        note: '',
      }

      // 빈 행을 rowData에 추가
      this.rowData.push(emptyRow)

      // AG-Grid API를 사용해 빈 행 추가
      this.gridApi.applyTransaction({ add: [emptyRow] })

      this.calculateTotals()
      // 데이터가 추가된 후 LocalStorage에 저장
      this.saveData()
    },
    // 삭제
    deleteRow() {
        // 선택된 행을 가져옴
        const selectedRows = this.gridApi.getSelectedRows()
        if (selectedRows.length === 0) {
            alert('삭제할 행을 선택하세요!')
            return
        }

        // 선택된 행을 rowData에서 제거
        this.rowData = this.rowData.filter(row => !selectedRows.includes(row))

        // AG-Grid에서 선택된 행을 삭제
        this.gridApi.applyTransaction({ remove: selectedRows })

        // 데이터가 삭제된 후 LocalStorage에 저장
        this.saveData()
    },
    // 붙여넣기 이벤트 처리
    onPaste(params) {
        // 붙여넣기된 범위
        const pasteData = params.data
        console.log('붙여넣기된 데이터:', pasteData)

        // 필요한 경우 추가 로직을 여기에 추가 (예: 붙여넣기된 데이터 검증)
        this.saveData()
    },
    // 소계를 계산하는 메서드
    calculateTotals() {
      let totalAmount = 0

      // rowData의 금액을 합산
      this.rowData.forEach(row => {
        if (row.amount && !isNaN(row.amount)) {
          totalAmount += Number(row.amount)
        }
      })

      // 총합 업데이트
      this.totalAmount = totalAmount

      // 소계 행 추가
      this.displayData = [...this.rowData] // 기존 데이터 유지
    },

    // LocalStorage에서 데이터를 불러오는 메서드
    loadData() {
      const savedData = localStorage.getItem('AccountBook_')
      return savedData ? JSON.parse(savedData) : [
        { date: '2024-09-01', item: 'Groceries', amount: 30000, note: 'Supermarket' },
        { date: '2024-09-02', item: 'Transport', amount: 5000, note: 'Bus fare' },
        { date: '2024-09-03', item: 'Rent', amount: 500000, note: 'Monthly rent' },
        { date: '2024-09-04', item: 'Dining Out', amount: 20000, note: 'Restaurant' },
        { date: '2024-09-05', item: 'Utilities', amount: 100000, note: 'Electricity bill' },
      ]
    },
    // LocalStorage에 데이터를 저장하는 메서드
    saveData() {
      localStorage.setItem('AccountBook_', JSON.stringify(this.rowData))
    },// 데이터 내보내기 (Export to JSON)
    exportData() {
      var date = new Date()
      var year = date.getFullYear()
      var month = ("0" + (1 + date.getMonth())).slice(-2)
      var day = ("0" + date.getDate()).slice(-2)

      const nowDate = year + month + day
      const dataStr = JSON.stringify(this.rowData)
      const dataUri = 'data:application/jsoncharset=utf-8,' + encodeURIComponent(dataStr)

      const exportFileDefaultName = 'AccountBook_ (' + nowDate + ').json'

      const linkElement = document.createElement('a')
      linkElement.setAttribute('href', dataUri)
      linkElement.setAttribute('download', exportFileDefaultName)
      linkElement.click()
    },
    // 데이터 불러오기 (Import from JSON)
    importData(event) {
      const file = event.target.files[0]
      const reader = new FileReader()

      if (file.name.indexOf('AccountBook_') > -1) {
        console.log(file.name + 'success')
      } else {
        alert('AccountBook_인 파일을 선택해 주세요')
        return
      }

      reader.onload = (e) => {
        const importedData = JSON.parse(e.target.result)
        this.rowData = importedData
        this.gridApi.setRowData(this.rowData)
        this.saveData()
      }
      console.log(file)

      reader.readAsText(file)
    },
  },
}