import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '../views/HomePage.vue'
import AboutPage from '../views/AboutPage.vue'
import AccountBook from '../views/AccountBook/AccountBook.vue'
import PG0001 from '../views/PG0001/PG0001.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',  // URL에서 해시(#) 없이 사용
  routes: [
    {
      path: '/',
      name: 'accMain',
      component: PG0001
    },
    {
      path: '/home',
      name: 'HomePage',
      component: HomePage
    },
    {
      path: '/about',
      name: 'AboutPage',
      component: AboutPage
    },
    {
      path: '/account',
      name: 'AccountBook',
      component: AccountBook
    },
    {
      path: '/PG0001',
      name: 'PG0001',
      component: PG0001
    }
  ]
})