// import '@toast-ui/vue-grid/dist/toastui-vue-grid.css';
import { Grid as ToastGrid } from '@toast-ui/vue-grid';

export default {
  name: 'AccountBook',
  components: {
    'toast-ui-grid': ToastGrid,
  },
  data() {
    return {
      // 그리드에 표시할 데이터
      gridData: this.loadData(),
      gridHeight: 500, // 기본 그리드 높이
      // 각 열의 설정
      /*
      {
        header: "분류",
        name: "workGroup",
        align: "center",
        width: "180",
        formatter: "listItemText",
        editor: {
            type: "select",
            options: {
                listItems: workGroupCategory
            }
        },
      */
      columnOptions: {
        minWidth: 50,
      },
      columns: [
        { header: '이름', name: 'name', sortable: true, editor: 'text', rowSpan: true },
        { header: '분류', name: 'class', sortable: true, editor: 'text', rowSpan: true, width: 100 },
        { header: '내역', name: 'task', sortable: true, editor: 'text', width: 120 },
        { header: '금액 (₩)', name: 'amount', sortable: true, editor: 'text', width: 80 },
        { header: '항목 합계', name: 'sumAmount', sortable: true, editor: 'text', rowSpan: true, width: 80 },
        // { header: '결제일', name: 'date', sortable: true, editor: 'text' },
        { header: '결제분류', name: 'dateClass', sortable: true, width: 80,
          editor: {
            type: 'select',
            options: {
              listItems: [
                { text: '매월10일', value: '매월10일' },
                { text: '매월25일', value: '매월25일' },
                { text: '매년', value: '매년' }
              ]
            }
          }
        },
        { header: '출금처', name: 'withdrawal', sortable: true, editor: 'text', width: 80 },
        { header: '메모', name: 'note', sortable: true, editor: 'text', width: 150 }
      ],
      // rowSpan의 경우 Data에서 직접 Controller 가능함 - (https://nhn.github.io/tui.grid/latest/tutorial-example06-attributes)
      totalAmount: 0, // 총합 변수
      insuranceTotal: 0, // 보험
      saveTotal: 0, // 적금
      monthTotal: 0, // 월지출
      yearTotal: 0, // 연지출
      // 소계 표시 설정
      summary: {
        height: 40,
        position: 'bottom',
        columnContent: {
          amount: {
            template: (valueMap) => `합계: ${valueMap.sum}`
          },
          sumAmount: {
            template: () => this.totalAmount + 'd'
          }
        }
      },
    };
  },
  methods: {
    // 그리드 데이터 불러오기
    loadData() {
      const savedData = localStorage.getItem('PG0001');
      return savedData ? JSON.parse(savedData) : [
        { date: '2024-09-01', item: 'Groceries', amount: 30000, note: 'Supermarket', name: 'john', class: '적금', task: '청년도약적금' },
        { date: '2024-09-02', item: 'Transport', amount: 5000, note: 'Bus fare', name: 'john', class: '적금', task: '청년도약적금' },
        { date: '2024-09-03', item: 'Rent', amount: 500000, note: 'Monthly rent', name: 'john', class: '적금', task: '청년도약적금' },
        { date: '2024-09-04', item: 'Dining Out', amount: 20000, note: 'Restaurant', name: 'john', class: '적금', task: '청년도약적금' },
        { date: '2024-09-05', item: 'Utilities', amount: 100000, note: 'Electricity bill', name: 'john', class: '적금', task: '청년도약적금' }
      ]
    },
    // 셀 편집이 끝났을 때 호출되는 메서드
    onCellEditEnded(event) {
      const { changes } = event; // 변경된 내용들
      changes.forEach(({ rowKey, columnName, value }) => {
        console.log(`Cell Edit Ended - rowKey: ${rowKey}, column: ${columnName}, new value: ${value}`);
        if (columnName === 'amount' && value === '0') {
          // amount가 0이면 메모를 'free'로 설정
          this.$refs.grid.invoke('setValue', rowKey, 'note', 'free');
        }
      });

      this.saveData(); // 변경된 데이터 저장
      this.calculateTotals(); // 총합 다시 계산
    },

    // 새 행 추가
    addRow() {
      this.$refs.grid.invoke('appendRow', { date: '', item: '', amount: 0, note: '' });
      this.saveData(); // 저장
    },

    // 선택된 행 삭제
    deleteRow() {
      const checkedRows = this.$refs.grid.invoke('getCheckedRows');
      if (checkedRows.length === 0) {
        alert('삭제할 행을 선택하세요!');
        return;
      }

      // 선택된 행 삭제
      checkedRows.forEach((row) => {
        this.$refs.grid.invoke('removeRow', row.rowKey);
      });
      this.saveData(); // 삭제 후 저장
    },

    // 데이터 저장 (LocalStorage)
    saveData() {
      const gridData = this.$refs.grid.invoke('getData');
      localStorage.setItem('PG0001', JSON.stringify(gridData));
      this.calculateTotals();
    },

    // 데이터 내보내기 (Export to JSON)
    exportData() {
      const gridData = this.$refs.grid.invoke('getData');
      const dataStr = JSON.stringify(gridData);
      const blob = new Blob([dataStr], { type: 'application/json' });
      const link = document.createElement('a');
      link.href = URL.createObjectURL(blob);
      link.download = 'PG0001.json';
      link.click();
    },

    // 데이터 불러오기 (Import from JSON)
    importData(event) {
      const file = event.target.files[0];
      const reader = new FileReader();

      reader.onload = (e) => {
        const importedData = JSON.parse(e.target.result);
        this.gridData = importedData;
        this.$refs.grid.invoke('resetData', importedData);
        this.saveData(); // 저장
      };

      reader.readAsText(file);
    },

    // 총합 계산
    calculateTotals() {
      const gridData = this.$refs.grid.invoke('getData');
      // 총 금액
      this.totalAmount = gridData.reduce((sum, row) => sum + Number(row.amount || 0), 0);

      // 보험 금액
      this.insuranceTotal = gridData.reduce((sum, row) => {
        let amount = row.class === '보험' ? Number(row.amount) : 0
        return sum + Number(amount);
      }, 0);
      // 적금 금액
      this.saveTotal = gridData.reduce((sum, row) => {
        let amount = row.class === '적금' ? Number(row.amount) : 0
        return sum + Number(amount);
      }, 0);
      // 매월 금액
      this.monthTotal = gridData.reduce((sum, row) => {
        let amount = row.dateClass.indexOf('매월') > -1 ? Number(row.amount) : 0
        return sum + Number(amount);
      }, 0);
      // 매년 금액
      this.yearTotal = gridData.reduce((sum, row) => {
        let amount = row.dateClass.indexOf('매년') > -1 ? Number(row.amount) : 0
        return sum + Number(amount);
      }, 0);
      
    },
    updateGridHeight() {
      const width = window.innerWidth;
      if (width < 768) {
        this.gridHeight = 300; // 작은 화면에서는 그리드 높이를 줄임
      } else {
        this.gridHeight = 500; // 기본 그리드 높이
      }
    },
  },
  mounted() {
    this.calculateTotals(); // 초기화 시 총합 계산
    this.updateGridHeight();
    window.addEventListener('resize', this.updateGridHeight);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.updateGridHeight);
  },
};